import React, { Component } from 'react';
// import Country from './Country';
import Fab from '@mui/material/Fab';
import AddIcon from '@mui/icons-material/Add';


class NewCountry extends Component {
    state = {
        showForm: false,
        name: '',
        gold: 0,
        silver: 0,
        bronze: 0,
    }

   toggleForm = () => this.setState({ showForm : !this.state.showForm });
   handleChange = (e) => this.setState({ [e.target.name ] : e.target.value});
   saveCountry = () => {
    const { name, gold, silver, bronze } = this.state;
    this.props.onAdd(name, gold, silver, bronze);
    this.toggleForm();
   }
    
    render() {
        const {showForm, name, gold, silver, bronze} = this.state;
        return (
            
            <div className='New-country'>
                {
                    (showForm) ?
                    <form>
                        <input type='text' id='name' name='name' value={ name } onChange={ this.handleChange } placeholder='Country' autoComplete='off'></input>
                        <input type='text' id='gold' name='gold' value={ gold } onChange={ this.handleChange } placeholder='Country' autoComplete='off'></input>
                        <input type='text' id='silver' name='silver' value={ silver } onChange={ this.handleChange } placeholder='Country' autoComplete='off'></input>
                        <input type='text' id='bronze' name='bronze' value={ bronze } onChange={ this.handleChange } placeholder='Country' autoComplete='off'></input>

                        <button type='button' onClick={ this.saveCountry }>Save</button>
                        <button type='button' onClick={this.toggleForm}>Cancel</button>
                    </form>
                    :
                    <Fab aria-label="add" onClick={this.toggleForm}>
                        <AddIcon/>
                    </Fab>
                    // <span onClick={this.toggleForm} className='Toggle-form'>New Country</span>
                }
            </div>
        )
    }
}

export default NewCountry;